import heroHeadingAnimation from "./heroHeadingAnimation";
import { typeTextOn } from "./typeTextOn";
import {
  onlyPlayWhenVisible,
  pauseAnimations,
  resumeAnimations,
} from "./utils";
import { ytdefer_setup } from "./ytdefer";
document.addEventListener("DOMContentLoaded", () => {
  // all pages have a hero heading animation. The hero has a slightly more complex intersectionObserver setup which is contained in the function.
  heroHeadingAnimation();

  // ticker tape effect (CSS animation, found on 'yesterday' and 'tomorrow' pages)
  const tickerTapeSelector = ".ticker-tape__link";
  const tickerTapeLink = document.querySelector(tickerTapeSelector);

  onlyPlayWhenVisible(tickerTapeSelector);

  // the ticker tape should pause on hover (animation-play-state: paused; in the CSS isn't working reliably for some reason)
  tickerTapeLink?.addEventListener("mouseover", () =>
    pauseAnimations(tickerTapeSelector),
  );
  tickerTapeLink?.addEventListener("mouseout", () =>
    resumeAnimations(tickerTapeSelector),
  );

  // typewriter effect
  const typingSelector = ".typing__link";
  const typingLink = document.querySelector(typingSelector);
  if (typingLink) {
    typeTextOn();
    onlyPlayWhenVisible(typingSelector);
  }

  /* ytdefer is an alternative to loading the 2.5MB base.js from the standard YouTube embed */
  ytdefer_setup();
});
